body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html{
  height: 100%;
}
input {
  outline: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* css */
.footerNav {
    position: fixed;
    bottom: 0;
    left:0;
    right:0;
    width: 100%;
    height: 44px;
    background: #fafafa;
    border-top: 1px solid #e6e6e6;
    z-index: 999;
}
.tab5 {
    width: 100%;
    height: 44px;
}
.tab5 li {
    width: 25%;
    float: left;
    text-align: center;
}
.tab5 li a {
    width: 57px;
    height: 44px;
    display: inline-block;
}
.tab4 li a img, .tab5 li a img {
    max-width: 100%;
    max-height: 100%;
}
