.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

*{margin: 0;padding: 0;}
ul{list-style: none;}
a{
  text-decoration: none;
}
/* CSS */
.wrapper {
    width: 100%;
    position: relative;
    margin: 0 auto;
    height: 1.5rem;
    max-width: 85.375rem;
}
.searchHead {
    position: relative;
    padding: .175rem 0;
    height: 1.1rem;
    line-height: 1.1rem;
    font-size: .7rem;
    background-color: #ff4d8e;
    z-index: 50;
    text-align: center;
    
}
.left{
        position:relative;
        left: -4.133333rem;
        top:-0.133333rem;
    }
.searchHead .icon-back {
    background: url(/resources/images/m/search/icon_back.png) no-repeat center;
    background-size: 1rem auto;
}
.searchHead .icon-close {
    background: url(/resources/images/m/search/icon_close.png) no-repeat center;
    background-size: 1rem auto;
}
.searchHead .icon-lt {
    position: absolute;
    top: .175rem;
    left: 0;
    width: 1.5rem;
    height: 1.5rem;
}
/* .searchHead .icon-home {
    background: url(/resources/images/m/search/icon_home.png) no-repeat center;
    -webkit-background-size: 1rem auto;
    background-size: 1rem auto;
} */
.searchHead .search{
    font-size: 0.8rem;
    color:#f6f6f6; 
}
.searchHead .iptbox {
    border-radius: .2rem;
    position: relative;
    margin: 0 1.0rem 0 1.0rem;
    height: 110%;
    background-color: #fff;
    overflow: hidden;
    margin-top: -1.2rem;
}

.searchHead .iptbox input {
    transition: all .3s ease;
    position: relative;
    z-index: 5;
    padding: 0 0.5rem;
    border: none 0;
    background: 0 0;
    width: 100%;
    height: 1.1rem;
    line-height: 1.1rem;
    font-size: 0.5rem;
    font-family: inherit;
    color: #999;
    outline: 0;
    -webkit-appearance: none;
}


/*   CSS */
.classify {
    position: fixed;
    top: 1.5rem;
    bottom: 0;
    width: 100%;
    height: 100%;
    max-width:40rem;
}
.classify .sidebar {
    position: absolute;
    top: -0.346667rem;
    bottom: 0;
    width: 3rem;
    background-color: #f6f6f6;
    overflow: auto;
    margin-bottom: 3rem;
}
.classify .sidebar::-webkit-scrollbar {
    display: none;
}
/*  */
.classify .sidebar li.current {
    background-color: #fff;
    color: #ff4d8e;
}
.classify .sidebar li {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 1.5rem;
    width: 2.875rem;
    font-size: 0.5;
    cursor: pointer;
}
.classify .sidebar li.current:before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: .125rem;
    background-color: #ff4d8e;
}

/*  CSS */
.classify .content {
    position: absolute;
    top: -0.346667rem;
    left: 2.9rem;
    right: 0;
    bottom: 3.2rem;
    background-color: #fff;
    overflow: auto;
}
.classify .content .scroll {
    overflow: hidden;
    background: #fff;
}
.classify .content h3 {
    margin: 0.3rem 0 0 0.3rem;
    line-height: 1;
    font-weight: 400;
    font-size: 0.4rem;
    color: #333;
}
.classify .content .items>a {
    display: inline-block;
    vertical-align: top;
    margin: 0.2rem .25rem .25rem 0.5rem;
    font-size: 0.2rem;
    color: #252525;
    width: 1.3rem;
    text-align: center;
}
.classify .content .items>a .imgbox {
    display: flex;
    justify-content: center;
    align-items: center;
    display: block;
    width: 1.813333rem;
    height: 1.813333rem;
}
.classify .content .items>a .imgbox img {
    max-width: 100%;
    max-height: 100%;
}
.classify .content .items>a p {
    margin-top: 2px;
    font-size: 0.3rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    font-family: "Helvetica Neue",Helvetica,STHeiTi,Arial,sans-serif;
    word-break: break-word;
    line-height: 1.3;
    color: grey;
    text-align: center;
}
